.footer {
	background-color: #f8f8f8;
	padding: 28px 0 19px;
	z-index: 3;
	position: relative;

	&__inner {
		display: flex;
		flex-wrap: wrap;
	}

	&__col {
		width: 25%;

		.menu-footer__item {
			width: 205px;
		}

		&_3 {
			width: 35%;

			.menu-footer__list {
				display: flex;
				flex-wrap: wrap;
				height: 181px;
			}

			.menu-footer__title {
				width: 100%;
			}

			.menu-footer__item {
				width: 50%;
			}
		}

		&_last {
			width: 15%;
			display: flex;
			flex-direction: column;
		}
	}

	&__row {
		width: 100%;
		display: flex;
		padding-top: 23px;
		align-items: center;
		flex: 1 1 100%;
		justify-content: space-between;
	}

	&__copyright {
		font-size: 12px;
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		line-height: 1.83;
		letter-spacing: normal;
		text-align: left;
		color: #b0b0b0;
	}

	&__button {
		height: 34px;
		padding: 4px 10px 4px 11px;
		border-radius: 3px;
		background-color: #394791;
		color: #ffffff;
		font-family: "Roboto Condensed";
		font-size: 14px;
		font-weight: 400;
		color: #ffffff;
		display: flex;
		align-items: center;
		text-transform: uppercase;
	}

	&__text {
		color: #666666;
		font-family: Arial;
		font-size: 13px;
		line-height: 22px;
		text-align: right;
	}

	&__tel {
		color: #333333;
		font-size: 20px;
		font-weight: 400;
		line-height: 26px;
		text-align: right;
	}

	&__email {
		color: #394791;
		font-family: Arial;
		font-size: 13px;
		line-height: 22px;
		text-align: right;
		margin: 1rem 0;

		i {
			color: #666666;
			font-style: normal;
		}
	}

	&__telephones {
		width: 25%;
		display: flex;
		min-width: 203px;
		justify-content: flex-end;
	}

	@media screen and (max-width: 768px) {
		.footer {
			&__col {
				width: 33.3%;
				margin-bottom: 28px;

				&_last {
					width: 66.6%;
					margin-bottom: 14px;
					display: flex;
					flex-wrap: wrap;
				}
			}

			&__text {
				width: 50%;
				flex: 1 1 150px;

				&_first {
					order: 1;
				}

				&_two {
					order: 2;
				}

				&_three {
					order: 4
				}
			}

			&__tel {
				width: 50%;
				order: 3;
			}
		}
	}

	@media screen and (max-width: 480px) {
		padding: 0 0 19px;

		* {
			transition: 0.3s;
		}

		.menu-footer__list-margin .menu-footer__title {
			margin-top: 0 !important;
		}

		&__col {
			width: 100% !important;
			margin-bottom: 0 !important;

			&_three {
				.menu-footer {
					border-bottom: 1px solid #494949;
					margin-bottom: 20px;

					&__title {
						padding-bottom: 16px !important;
					}
				}
			}
		}

		&__row {
			flex-wrap: wrap;
		}

		&__copyright {
			width: 100%;
			text-align: center;
			margin-bottom: 16px;
		}

		&__telephones {
			margin: 0 auto;
			width: unset;
		}

		&__button {
			width: 288px;
			height: 32px;
			text-align: center;
			justify-content: center;
		}

		&__text {
			width: 100% !important;
			flex: 1 1 auto !important;

			&_first {
				order: 1;
			}

			&_two {
				order: 3 !important;
			}

			&_three {
				order: 4
			}
		}

		&__tel {
			width: 100% !important;
			flex: 1 1 auto !important;
			order: 2 !important;
		}

		.menu-footer {
			height: 53px;
			overflow: hidden;

			&__title {
				padding: 16px;
				border-top: 1px solid #494949;
				background: url("https://icongr.am/entypo/chevron-down.svg?color=eeeeee") 97%/18px no-repeat;
			}

			&__item {
				padding: 5px 16px;
			}

			&__list {
				padding-bottom: 10px;
			}
		}

		.active {
			height: unset;
			overflow: unset;

			.menu-footer__title {
				background: url("https://icongr.am/entypo/chevron-up.svg?color=eeeeee") 97%/18px no-repeat;
			}
		}
	}
}

.menu-footer {
	&__title {
		font-size: 18px;
		font-weight: 400;
		line-height: 36px;
		color: #333333;
	}

	&__link {
		color: #666666;
		font-family: Arial;
		font-size: 13px;
		line-height: 20px;
	}

	&__list {
		margin: 0;
		padding: 0;
		list-style-type: none;

		&-margin {
			.menu-footer__title {
				margin-top: 20px;
			}
		}
	}
}

